/* sizes */
#main-wrap > div { min-height: 100px; }


#header,
#footer {
}

/* layout */
#main-wrap {
    /* overflow to handle inner floating block */
    overflow: hidden;
}

#sidebar {

}

#content-wrap {
}   
.header_content {
}
.mytable {
    border-style: solid;
}

.Modal {              /* Modal Box */
    position: fixed;
    z-index: 99;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /*overflow: auto;*/
    height: 75%;
    background-color : #EAE6C7;
    border-radius: 4px;
  }
  .Modal_overlay {     /* Below layer of Modal Box */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1; 
    position: fixed;
    background-color: rgba(0, 0, 0, 0.75);
  }
  .Modal_content {     /* Everything Inside The Modal Box */
    position: relative;
    background-color: #EAE6C7;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  .Modal_header {      /* Header Modal Box */
    padding: 32px;
    height: 10%;
    background-color: #EFEFEF;
  }
  .Modal_close {
    position: absolute;
    top: 16px;
    right: 16px;
    color: #333333;
    font-size: 32px;
    text-decoration: none;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .Modal_body {
    position: relative;
    margin: auto;
    height: 60%;
    padding: 8px;
    background-color: #EFEFEF;  
  }
  .Modal_body_text {
     /* background-color: yellow; */
    position: absolute;
    top: 80%;
    left: 50%;
    width: 100%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
  }
  .Modal_footer {
    text-align: center;
    padding: 16px;
    background-color: #EFEFEF;
    height: 20%;
    
  }
  .Modal_btn {
    color: #E0E0E0;
    background-color: #191919;
    width: 128px;
    font-size: 20px;
    top: 0;
    left: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: solid;
    border-radius: 40px;
    line-height: 32px;
  }
  .Modal_spinner {
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .Modal_table {
    margin: 0 auto;
    width: 240px;
    height: 240px;
    font-size: 20px;
    text-align: center;
  }

  #left, #right { 
    background: white; 
    font-size: 10px;
    height: 20px;    
    border : solid
  } 
  
  #left {
    float: left;
    width: 40%;
  }
  
  #right {
      float: right;
      width: 40%;
      font-family: SanamDeklen_chaya;      
  }  


